import React, { useState } from 'react';
import { Modal, Button } from 'react-bootstrap';

function GraphicDesignerDirection() {
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    return (
        <>
            <Button variant="primary" onClick={handleShow}>
                Look more...
            </Button>

            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>Graphic Designer</Modal.Title>
                </Modal.Header>
                <Modal.Body>BRSV developer Skills</Modal.Body>
                <Modal.Body>Woohoo, you're reading this text in a modal! wait for redux please)</Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Close
                    </Button>
                </Modal.Footer>
            </Modal>
        </>
    )
}
export default GraphicDesignerDirection